.header{
  background-color: #fff;
  display: grid;
  grid-template-columns: 8fr 2fr 0.3fr;
  padding: 5px;
  position: fixed;
  top:0;
  z-index: +1;
  .actions{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    .cartCounter{
      color: greenyellow;
      font-size: 22px;
      font-weight: bold;
    }
    p:hover{
        color:greenyellow;
        cursor: pointer;
    }
  }
}





@media (max-width:600px){
  .header{
    background-color: red;
  }
  .actions{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-right:20px;
    p{
      margin: 5px;
    }
  }
}