$h1:45px;

a{
    text-decoration: none;
}
.promotions{
    background-color: grey;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 40px;
}
.categories{

    padding: 20px;
    
    div{
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        column-gap: 10px;
        text-align: center;
        padding :5px;
        h4{
            background-color: rgb(32, 233, 32);
            font-size: 1em;
            color: white;
            &:hover{
                color: blue;
            }
        }   
    }
}
.sliderContainer{
    padding: 20px 40px;
    background-color: rgb(228, 228, 228);
    width: 90%;
    margin: auto;
    h2{
        padding: 15px;
    }
}
.hiddenSlider{
    display: none;
} 
