.advertSection{
    padding: 10px;
    background-color: red;
}
.hspacer{
    height:50px;
}

@media (max-width:600px){
    .categories{display: none;}
    .categories{
        padding: 5px;
        div{
            display: grid;
            grid-template-columns: 1fr 1fr;
            text-align: center;
            margin: 3%;
            h4{
                margin: 5px;
            }
        }
    }
    .categorySlider{
        display: none;
    }
    .hiddenSlider{
        display: grid;
    }
}